import React from 'react';
import { GatsbyImage } from 'gatsby-plugin-image';
import { useSiteMetadata } from '../../hooks/useSiteMetadata';
import * as classes from './style.module.css';
<script src="https://platform.linkedin.com/badges/js/profile.js" async defer type="text/javascript"></script>

export function AuthorSnippet(): React.ReactElement {
    const { author, avatar, bio } = useSiteMetadata();
    return (
        <div className={classes.AuthorSnippet}>
            {avatar?.childImageSharp?.gatsbyImageData ? (
                <GatsbyImage image={avatar.childImageSharp.gatsbyImageData} alt={author} className={classes.Avatar} />
            ) : null}
            <div className={classes.Description}>
                {/* <span className={classes.WrittenBy}>
                    Author: <u>{author}</u>
                    <div className="badge-base LI-profile-badge" data-locale="en_US" data-size="medium" data-theme="dark" data-type="VERTICAL" data-vanity="rahul-majumdar" data-version="v1"><a className="badge-base__link LI-simple-link" href="https://www.linkedin.com/in/rahul-majumdar?trk=profile-badge">Rahul Majumdar</a></div>              
                </span>
                <p className={classes.Bio}>{bio}</p> */}
            </div>
        </div>
    );
}
